const makeChipStyles = ({
  avatarStyle,
  chipStyle,
  chipMaxWidth
}) => makeStyles(theme => ({
  gridWrapper: {
    width: 'unset',
    alignItems: 'center',
    [theme.breakpoints.down('md')]: {
      width: '100%',
    }
  },
  item: {
    justifyContent: 'flexStart'
  },
  avatarStyle,
  chipStyle: {
    margin: theme.spacing(0, 1, 0, 0),
    [theme.breakpoints.down('md')]: {
      margin: theme.spacing(0, 0, 1, 0),
    },
    minWidth: chipMaxWidth ? `${chipMaxWidth}px` : '9em',
    justifyContent: 'flex-start',
    ...chipStyle
  }
}));

