import React, { memo } from 'react';
import { Typography } from '@material-ui/core'

// 强调性文字 
const EmphasizeText = memo((props) => {
  return (
    <Typography
      display="inline"
      variant="body2"
      component="span"
      color="secondary"
      {...props}
    />
  )
})

export default EmphasizeText