import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import MyAvatarPNG from "../../images/avatar.png";
import {
  List,
  Avatar,
  Typography,
  ListItem,
  ListItemText,
  ListItemAvatar,
} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  avatar: {
    width: theme.spacing(12),
    height: theme.spacing(12),
    marginRight: theme.spacing(2),
  },
}));

function getAge() {
  const birthday = new Date("1996-09-01");
  const now = new Date();
  let diff = 1;

  if (now.getMonth() >= birthday.getMonth() && now.getDate() >= birthday.getDate()) {
    diff = 0;
  }
  return now.getFullYear() - birthday.getFullYear() - diff;
}

const age = getAge();

export default function PersonalRecognition() {
  const classes = useStyles();
  return (
    <List>
      <ListItem>
        <ListItemAvatar>
          <Avatar
            variant="circular"
            alt="avatar"
            className={classes.avatar}
            src={MyAvatarPNG}
          />
        </ListItemAvatar>
        <ListItemText>
          <Typography variant="h5" color="primary">
            黎子恒
          </Typography>
          <Typography
            variant="body1"
          >
            1996年生({age}岁)
          </Typography>
          <Typography
            variant="body2"
          >
            web前端开发工程师
          </Typography>
        </ListItemText>
      </ListItem>
    </List>
  );
}
